body {
  margin: 0;
  color: #2493bd;
  background-color: #f5f4ec;
  font-family:
          Roboto,
          -apple-system,
          BlinkMacSystemFont,
          'Helvetica Neue',
          'Segoe UI',
          'Oxygen',
          'Ubuntu',
          'Cantarell',
          'Open Sans',
          sans-serif;
}
.container_select{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0 30px 0 30px;
}
.rates{
    margin:  auto;
    color: #26282e;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    align-items: center;
    justify-items: center;
}
h1{
    text-align: center;
    margin: 10px 0 30px 0;
}
p{
    font-size: 0.8em;
}
@media  (max-width: 767px){
    .container_select{
        display: flex;
        height: 200px;
        justify-content: space-between;
        align-items: center;
        padding: 0 30px 0 30px;
        flex-direction: column;

    }
    .rates{
        margin:  auto;
        color: #26282e;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        align-items: center;
        justify-items: center;
    }
}
@media  (max-width: 576px){
    .container_select{
        display: flex;
        height: 200px;
        justify-content: space-between;
        align-items: center;
        padding: 0 30px 0 30px;
        flex-direction: column;

    }
    .rates{
        margin:  auto;
        color: #26282e;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        align-items: center;
        justify-items: center;
    }
}

